@import "./var.css";

* {
    margin: 0;
    padding: 0;
}

html,
body {
    height: 100%;
    /* for touch screen */
    /* touch-action: none; */
}

.sponsor-div {
    overflow: hidden;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /*background: #111;*/
    -webkit-perspective: 1000px;
    perspective: 1000px;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
}

#drag-container, #spin-container {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-transform: rotateX(-10deg);
    transform: rotateX(-10deg);
}

#drag-container img, #drag-container video {
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    position: absolute;
    left: 0;
    top: 150px;
    width: 100%;
    height: 100%;
    line-height: 200px;
    font-size: 50px;
    text-align: center;
    /*-webkit-box-shadow: 0 0 8px #fff;*/
    /*box-shadow: 0 0 8px #fff;*/
    /*-webkit-box-reflect: below 10px linear-gradient(transparent, transparent, #0005);*/
}

#drag-container img:hover, #drag-container video:hover {
    -webkit-box-shadow: 0 0 15px #fffd;
    box-shadow: 0 0 15px #fffd;
    /*box-reflect: below 10px linear-gradient(transparent, transparent, rgba(0, 0, 0, 0.81));*/
    /*box-reflect: linear-gradient(#61dafb , #282c34)*/
}

    #drag-container p {
        font-family: Serif;
        position: absolute;
        top: 100%;
        left: 50%;
        -webkit-transform: translate(-50%,-50%) rotateX(90deg);
        transform: translate(-50%,-50%) rotateX(90deg);
        color: #fff;
    }

#ground {
    width: 200px;
    height: 200px;
    position: absolute;
    top: 200%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%) rotateX(90deg);
    transform: translate(-50%,-50%) rotateX(90deg);
    /*background: -webkit-radial-gradient(center center, farthest-side , #9993, transparent);*/
    /*background: radial-gradient(center center , farthest-side);*/
    /*background: radial-gradient(#9993, #9993);*/
    /*background: radial-gradient(center,rgba(0, 0, 0, 0.93), transparent) ;*/
    background: radial-gradient(closest-side, #8c8c8c, transparent);
}

#music-container {
    position: absolute;
    top: 0;
    left: 0;
}

@-webkit-keyframes spin {
    from{
        -webkit-transform: rotateY(0deg);
        transform: rotateY(0deg);
    } to{
          -webkit-transform: rotateY(360deg);
          transform: rotateY(360deg);
      }
}

@keyframes spin {
    from{
        -webkit-transform: rotateY(0deg);
        transform: rotateY(0deg);
    } to{
          -webkit-transform: rotateY(360deg);
          transform: rotateY(360deg);
      }
}
@-webkit-keyframes spinRevert {
    from{
        -webkit-transform: rotateY(360deg);
        transform: rotateY(360deg);
    } to{
          -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
      }
}
@keyframes spinRevert {
    from{
        -webkit-transform: rotateY(360deg);
        transform: rotateY(360deg);
    } to{
          -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
      }
}