.fade-in{
    -webkit-animation: fadeIn 1s alternate;
            animation: fadeIn 1s alternate;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.fade-out{
    -webkit-animation: fadeOut 1s alternate;
            animation: fadeOut 1s alternate;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.slide-up{
    -webkit-animation: slideUp 1s alternate;
            animation: slideUp 1s alternate;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.slide-down{
    -webkit-animation: slideDown 1s alternate;
            animation: slideDown 1s alternate;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes slideUp {
    0% {
        bottom: -15vh;
    }
    100% {
        bottom: 5vh;
    }
}

@keyframes slideUp {
    0% {
        bottom: -15vh;
    }
    100% {
        bottom: 5vh;
    }
}
@-webkit-keyframes slideDown {
    0% {
        bottom: 5vh;
    }
    100% {
        bottom: -15vh;
    }
}
@keyframes slideDown {
    0% {
        bottom: 5vh;
    }
    100% {
        bottom: -15vh;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
