.events{
    margin: 5vh 10vh;
}
.page-title{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: block;
    margin: 10vh auto;
    color: var(--dyuthi-color);
}

.event-row{
    margin: 10vh 0;
}
.gen-events{

}

.event-item{
    padding: 2vh;
    display:block;
    content: '';
    /* background-color: violet; */
}

.event-poster{
    width: 15em; 
    height: 10em;
    margin-bottom: 5vh;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.event-border{
    border: 2px dashed var(--color-shadow-light);
    height: 10em;
    width: 15em;
    position: absolute;
    top: 0vh;
    left: 1vw;
    z-index: -1;
    /* transform: scaleX(0) scaleY(0);   */
    -webkit-transition: -webkit-transform 250ms ease-in-out;
    transition: -webkit-transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
    -webkit-transform-origin:  0% 0%;
            transform-origin:  0% 0%;
}

.event-title{
    /* width: fit-content; */
    width: 10vw !important;
    float: right;
    font-size: 2.5vh;
    font-weight: bolder;
    position: absolute;
    right: 2em;
    bottom: 3em;
    background-color: var(--color-text-light);
    color: var(--color-text-dark);
    padding: 5px 10px;
    -webkit-transform: scale(0);
            transform: scale(0);  
    -webkit-transition: -webkit-transform 250ms ease-in-out;  
    transition: -webkit-transform 250ms ease-in-out;  
    transition: transform 250ms ease-in-out;  
    transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
}

.event-item:hover{
    cursor: pointer;
}
.event-item:hover .event-poster{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
.event-item:hover .event-title{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
} 
.event-item:hover .event-border{
    -webkit-transform: scaleX(1.1) scaleY(1.1);
            transform: scaleX(1.1) scaleY(1.1);
} 

.events-sec{
    /* display: block;
    width: fit-content; */
    margin: 1vh 12vw 10vh;
}
.dept-title{
    font-size: 4vh;
    margin: 4vh 8vw 5vh;
}

@media only screen and (max-width: 1280px){
    html, body{
        overflow-x:hidden !important;
        overflow-y:scroll !important;
    }
    .event-title{
        width: 32vw !important;
        word-wrap: break-word;
        -webkit-hyphens: auto;
            -ms-hyphens: auto;
                hyphens: auto;
        float: right;
        font-size: 2vh;
        font-weight: bolder;
        position: absolute;
        left:50vw;
        bottom: 5em;
        background-color: var(--color-text-light);
        color: var(--color-text-dark);
        padding: 5px 10px;
        /* transform: scale(1.1) !important;   */
        -webkit-transform: none;
                transform: none;
        -webkit-transition: -webkit-transform 250ms ease-in-out;
        transition: -webkit-transform 250ms ease-in-out;
        transition: transform 250ms ease-in-out;
        transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
    }
    .event-item:hover .event-title{
        -webkit-transform: scale(1.1) translateX(-1em);
                transform: scale(1.1) translateX(-1em);
    }
    .gen-events{
        display: block;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin: 1vh 12vw 10vh;
    } 
    .events-sec{
        /* display: block;
        width: fit-content; */
        margin: 1vh 12vw 10vh;
    }
    .dept-events{
        margin-top: 10vh;
    }
    .dept-title{
        font-size: 4vh;
        margin: 4vh 8vw 2vh;
    }
}
