body{
    background-color: var(--color-light);
    color: var(--color-text-light);
}
.nav{
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 1vh auto;
}
.nav ul{
    padding: none;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}
.nav ul li {
    list-style-type: none;
    display: inline;
}

.nav ul li a , .nav ul li a > span{
    display: inline;
    text-decoration: none;
    margin: 1vh 1vh;
    padding: 1.5vh 5px;
    color: var(--color-text-light);
    border-radius: 5px;
    font-size: 2vh;
}
.nav ul li a:hover > span{
    border-radius: 0;
    padding: 1.5vh 5px;
    border-bottom: dashed 2px var(--color-text-light);
}

.nav ul li a.active > span{
    border-radius: 0;
    color: var(--dyuthi-color);
    padding: 1.5vh 5px;
    border-bottom: dashed 2px var(--dyuthi-color);
}
.logo{
    display: block;
    margin: 8vh auto 0;
}

.logo-inline{
    display: inline-block;
    margin: 0 10vh;
    vertical-align: middle;
}

.logo-default{
    width: auto;
    height: auto;
}

.logo-large{
    height: 40vh;
}

.logo-medium{
    height: 20vh;
}

.logo-small{
    height: 10vh;
}
.logo-xsmall{
    height: 4vh;
    width: auto;
    display: block;
    margin: 0 auto !important;
}

@font-face {
    font-family: dyuthiFont;
    src: url(/static/media/swatch.94e33f36.ttf);
}
body{
    font-family: dyuthiFont !important;
    letter-spacing: 1.5px;
}

.title{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 4vh;
    text-align: center;
    font-weight: normal;
    margin: 5vh auto;
    padding: 5px 5px;
    border-radius: 5px;
    line-height: 4vh;
    color: #fff;
}

.event-date{
    color: #fff;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 20vh auto;
    display: block;
    font-size: 3vh;
    text-align: center;
}
.event-date .insta a{
    color: rgb(134, 134, 134);
    font-size: 2vh;
    display: block;
    margin-top: 3vh;
    text-decoration: none;
}
.event-date .gect{
    color: rgb(207, 207, 207);
